<template>
  <div v-if="showTool" class="right-tool-box">
    <div class="box-item" @click="showGroup(0)">
      <el-popover
        :value="groupVisible[0]"
        placement="left-start"
        width="360"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          度量
          <i class="el-icon-arrow-right" @click="closeGroup(0)"></i>
        </div>
        <div class="inner">
          <div class="title">
            <label class="label-box">当前对象：</label>
            <span v-text="getInfoTypeName()"></span>
          </div>
          <div class="inner-item">
            <label class="label-box">X</label>
            <el-input-number
              :value="info.left"
              size="mini"
              :controls="false"
              @change="v => info.left = v"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box">Y</label>
            <el-input-number
              :value="info.top"
              size="mini"
              :controls="false"
              @change="v => info.top = v"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box">宽</label>
            <el-input-number
              :value="info.width"
              size="mini"
              :min="0"
              :controls="false"
              @change="v => updateWidth(v)"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box">高</label>
            <el-input-number
              :value="info.height"
              size="mini"
              :min="0"
              :controls="false"
              @change="v => updateHeight(v)"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box">角度</label>
            <el-input-number
              :value="info.angle"
              size="mini"
              :min="0"
              :max="360"
              :controls="false"
              @change="v => info.angle = v"
            ></el-input-number>
          </div>
          <template v-if="info.type === 'group'">
            <div class="inner-item">
              <label class="label-box">成员宽</label>
              <el-input-number
                :value="memberWidth"
                size="mini"
                :controls="false"
                @change="v => upGroupMemberPos('width', v)"
              ></el-input-number>
              <label class="label-box">成员高</label>
              <el-input-number
                :value="memberHeight"
                size="mini"
                :controls="false"
                @change="v => upGroupMemberPos('height', v)"
              ></el-input-number>
            </div>
            <div class="inner-item">
              <label class="label-box">行间距</label>
              <el-input-number
                :value="memberSpaceTop"
                size="mini"
                :controls="false"
                @change="v => upGroupSize('row', v)"
              ></el-input-number>
              <label class="label-box">列间距</label>
              <el-input-number
                :value="memberSpaceLeft"
                size="mini"
                :controls="false"
                @change="v => upGroupSize('col', v)"
              ></el-input-number>
            </div>
          </template>
        </div>
        <i slot="reference" class="iconfont icon-resize-"></i>
      </el-popover>
    </div>
    <div class="box-item" @click="showGroup(1)">
      <el-popover
        :value="groupVisible[1]"
        placement="left"
        width="360"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          属性
          <i class="el-icon-arrow-right" @click="closeGroup(1)"></i>
        </div>
        <div class="inner">
          <template v-if="!infoIsSec">
            <div v-if="info.type === 'bed' || info.memberType === 'bed' " class="inner-item">
              <label class="label-box">宿舍</label>
              <el-input v-model="info.dormName" size="mini"></el-input>
              <label style="margin-left: 5px" class="label-box">楼层</label>
              <el-input v-model="info.floorName" size="mini"></el-input>
            </div>
            <div v-if="info.type === 'bed' || info.memberType === 'bed' " class="inner-item">
              <label class="label-box">类型</label>
              <el-select v-model="info.dormType" size="mini">
                <el-option v-for="(sex, s) in sexList" :key="s" :label="sex.txt" :value="sex.val"></el-option>
              </el-select>
              <label style="margin-left: 5px" class="label-box">备注</label>
              <el-input v-model="info.remark" size="mini"></el-input>
            </div>
            <div v-if="info.type === 'seat' || info.memberType === 'seat' " class="inner-item">
              <label class="label-box">房间号</label>
              <el-input v-model="info.roomNum" size="mini"></el-input>
              <label style="margin-left: 5px" class="label-box">备注</label>
              <el-input v-model="info.remark" size="mini"></el-input>
            </div>
            <div class="inner-item">
              <label class="label-box" style="width: 80px;margin-right: 10px">显示文本</label>
              <el-radio-group v-model="info.showText">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </div>
            
            <div
              v-if="info.member && info.member.length>0"
              class="inner-item"
              :class="info.member.length>4? 'scroll-container': ''"
            >
              <div v-for="(item, it) in info.member" :key="it">
                <div class="title">{{ item.rowIndex + 1 }}行{{ item.colIndex + 1 }}列:</div>
                <label class="label-box">序号</label>
                <input type="text" class="input-mini" :value="item.seatNum" @change="setNumber($event, item)"/>
                <label class="label-box">文本</label>
                <input type="text" class="input-mini" :value="item.seatTxt" @change="e => item.seatTxt=e.target.value"/>
              </div>
            </div>
            <div v-else class="inner-item">
              <label class="label-box">序号</label>
              <input type="text" class="input-mini" :value="info.seatNum" @change="setNumber($event, info)" />
              <label class="label-box">文本</label>
              <input type="text" class="input-mini" :value="info.seatTxt" @change="e => info.seatTxt=e.target.value"/>
            </div>
          </template>
          <template v-else>
            <div class="inner-item">
              <p>文本</p>
              <el-input v-model="info.seatTxt"></el-input>
            </div>
          </template>
        </div>
        <i slot="reference" class="iconfont icon-xingzhuang-wenzi"></i>
      </el-popover>
    </div>
    <div class="box-item" @click="showGroup(2)">
      <el-popover
        :value="groupVisible[2]"
        placement="left-start"
        width="365"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          上传
          <i v-if="!infoIsSec" class="el-icon el-icon-s-tools" @click="dialogVisible = true"></i>
          <i class="el-icon-arrow-right" @click="closeGroup(2)"></i>
        </div>
        <div v-if="!infoIsSec" class="inner inner-flex">
          <div v-for="(state, s) in info.stateList" :key="state.statusValue" class="upload-box">
            <div class="img-box" @click="uploadMultiple(state)">
              <img v-if="state.img" :src="getImagePath(state.img)">
            </div>
            <p>{{ state.statusText }}</p>
          </div>
        </div>
        <div v-else>
          <div class="inner">
            <div class="single-upload-box" @click="uploadSingle">
              <img v-if="info.bgImg" :src="getImagePath(info.bgImg)" alt="">
            </div>
          </div>
        </div>
        <i slot="reference" class="iconfont icon-tupiantianjia"></i>
      </el-popover>
    </div>
    
    <el-dialog
      title="座位状态管理"
      :visible.sync="dialogVisible"
      :modal="false"
      width="35%"
    >
      <div class="dialog-box">
        <p class="title">说明</p>
        <p>statusText - 座位状态</p>
        <p>statusValue - 座位状态对应的值</p>
        <p>couldSeat - 此状态下人员能否入座（1能、2否）</p>
        <p class="title">默认状态</p>
        <p><span>正常|1|1</span><span>已排|2|1</span></p>
        <p><span>锁定|3|2</span><span>禁用|4|2</span></p>
        
        <p class="title">自定义座位状态</p>
        <el-input
          v-model="info.statusKv"
          type="textarea"
          :rows="5"
          placeholder="statusText | statusValue | couldSeat 以|分隔，一行定义一种状态"
        ></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="saveStatus">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import eventBus from "_platform/designer/seat/util/eventBus";
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import {
    updateGroupMemberPosition,
    uploadImg,
    getMemberSpaceTop,
    getMemberSpaceLeft,
    createUploadFileInput
  } from "../func"
  import {mapState} from "vuex"
  
  export default {
    name: "RightTools",
    props: {},
    data() {
      return {
        showTool: false,
        sexList: [
          {val: null, txt: "请选择"},
          {val: 1, txt: "男宿舍"},
          {val: 2, txt: "女宿舍"},
        ],
        defaultStateList: [
          {
            img: "",
            statusText: "正常",
            statusValue: 1,
            couldSeat: 1,
          },
          {
            img: "",
            statusText: "已排",
            statusValue: 2,
            couldSeat: 1
          },
          {
            img: "",
            statusText: "锁定",
            statusValue: 3,
            couldSeat: 2
          },
          {
            img: "",
            statusText: "禁用",
            statusValue: 4,
            couldSeat: 2
          },
        ],
        info: {
          stateList: []
        },
        groupVisible: [false, false, false],
        visiblePointer: null,
        oldVisibleStatus: false,
        dialogVisible: false,
      }
    },
    computed: {
      ...mapState("seatDesign", {
        activeId: "activeId",
      }),
      memberWidth() {
        if (_.isArray(this.info.member)) {
          return this.info.member[0].width
        } else {
          return undefined
        }
      },
      memberHeight() {
        if (_.isArray(this.info.member)) {
          return this.info.member[0].height
        } else {
          return undefined
        }
      },
      memberSpaceTop() {
        if (_.isArray(this.info.member)) {
          return getMemberSpaceTop(this.info)
        } else {
          return undefined
        }
      },
      memberSpaceLeft() {
        if (_.isArray(this.info.member)) {
          return getMemberSpaceLeft(this.info)
        } else {
          return undefined
        }
      },
      infoIsSec() {
        return /^sec_/.test(this.info.type)
      }
      
    },
    watch: {
      activeId(val) {
        if (!val) {
          this.showTool = false;
          const arr = new Array(3);
          this.groupVisible = _.fill(arr, false);
        } else {
          eventBus.emit(eventBusEvent.GET_ACTIVE_SEAT, seat => {
            if (!_.isArray(seat.stateList) || seat.stateList.length === 0) seat.stateList = _.cloneDeep(this.defaultStateList);
            this.info = seat;
          });
          
          this.showTool = true;
          this.$nextTick(() => {
            this.$set(this.groupVisible, this.visiblePointer, this.oldVisibleStatus);
          })
        }
      },
      
      info: {
        handler(val) {
          this.$store.commit("seatDesign/changeSeatInfo", val);
        },
        deep: false
      }
    },
    methods: {
      uploadSingle(){
        const self = this;
        let upDOM = createUploadFileInput();
        $(upDOM).on("change", function () {
          if (!upDOM.files || upDOM.files.length === 0) return false;
    
          const formData = new FormData();
          formData.append("file", upDOM.files[0]);
          const callback = result => {
            //清除创建的input
            $(upDOM).val("");
            $(upDOM).remove();
            upDOM = null;
    
            if (result === "error")  return false;
            self.info.bgImg = result[0].relativePath;
          };
          uploadImg.call(self, formData, callback);
        })
        $(upDOM).click();
      },
      uploadMultiple(item) {
        const self = this;
        let upDOM = createUploadFileInput();
        upDOM.multiple = true;
        $(upDOM).on("change", function () {
          if (!upDOM.files || upDOM.files.length === 0) return false;
          
          const formData = new FormData();
          for (let i = 0; i < upDOM.files.length; i++) {
            let file = upDOM.files[i];
            formData.append("file_" + i, file);
          }
          uploadImg.call(self, formData, res => {
            //清除创建的input
            $(upDOM).val("");
            $(upDOM).remove();
            upDOM = null;
            
            if (res === "error") return false;
            
            if (res.length === 1) {
              item.img = res[0].relativePath;
            } else {
              self.info.stateList.forEach((item, index) => {
                if (res[index]) {
                  item.img = res[index].relativePath;
                }
              })
            }
  
            //如果是group对象
            if (self.info.type === "group"){
              self.info.isFrozenMemberSty = false;
              self.info.member.forEach(member => {
                member.stateList = self.info.stateList
              })
            }
          });
        })
        $(upDOM).click();
      },
      closeGroup(pointer) {
        this.visiblePointer = pointer;
        this.$set(this.groupVisible, pointer, false);
        this.oldVisibleStatus = false;
      },
      showGroup(pointer) {
        this.visiblePointer = pointer;
        const arr = new Array(3);
        this.groupVisible = _.fill(arr, false);
        this.$set(this.groupVisible, pointer, true);
        this.oldVisibleStatus = true;
      },
      saveStatus() {
        if (!this.info.statusKv){
          this.dialogVisible = false;
          return;
        }
        const arr = this.info.statusKv.split("\n");
        const defaultArr = _.cloneDeep(this.defaultStateList);
        let temp, obj, result = [];
        let queue = [];
        let judge = true;
        let msg ="";
        
        arr.some(item => {
          temp = item.split("|").map(str => {
            return str.replace(/(^\s*)|(\s*$)/g, "");    //去掉首尾的空格
          });
          if (+temp[2] !== 1 && +temp[2] !== 2) {
            judge = false
            msg = "statusText|statusValue|couldSeat  当中的couldSeat只能为1或2"
            return true
          }
          
          let sIndex = defaultArr.findIndex(a => a.statusValue === +temp[1]);
          if (sIndex !== -1){
            judge = false;
            msg = "你的 statusValue 与系统默认的冲突了"
            return true
          }
          
          let qIndex = queue.indexOf(temp[1]);
          if (qIndex !== -1){
            judge = false;
            msg = "你设置了重复的 statusValue"
            return true
          }
          queue.push(temp[1])
          
          obj = {
            img: "",
            statusText: temp[0],
            statusValue: +temp[1],
            couldSeat: +temp[2]
          };
          result.push(obj);
        });
        
        if (!judge) {
          this.saveStatusError(msg);
          return
        }
        
        const diffArr = _.differenceBy(result, defaultArr, "statusValue");
        
        this.info.stateList = _.concat(defaultArr, diffArr).sort((a, b) => a.statusValue - b.statusValue);
        //如果是group对象
        if (this.info.type === "group"){
          this.info.isFrozenMemberSty = false;
          this.info.member.forEach(member => {
            member.stateList = this.info.stateList
          })
        }
        this.dialogVisible = false;
      },
      saveStatusError(msg){
        this.info.statusKv = "";
        dsf.layer.alert(msg);
      },
      getImagePath(path) {
        return path ? dsf.url.getWebPath(path) : null;
      },
      getInfoTypeName() {
        switch (this.info.type) {
          case 'seat':
            return '座位'
          case 'bed':
            return '床位'
          case 'group':
            if (this.info.memberType === 'seat') return '座位组'
            if (this.info.memberType === 'bed') return '床位组'
            break;
          case 'sec_rect':
              return "矩形"
        }
      },
      updateWidth(width) {
        this.info.width = width;
        this.info.isFrozenSty = false;  //重新计算样式
        if (this.info.type === "group") {
          this.info.isFrozenMemberSty = false;  //重新计算成员样式
          updateGroupMemberPosition(this.info);
        }
      },
      updateHeight(height) {
        this.info.height = height;
        this.info.isFrozenSty = false;  //重新计算样式
        if (this.info.type === "group") {
          this.info.isFrozenMemberSty = false;  //重新计算成员样式
          updateGroupMemberPosition(this.info);
        }
      },
      upGroupMemberPos(type, val) {
        const group = _.cloneDeep(this.info);
        group.member.forEach(item => {
          type === "width" ?
            item.width = val :
            item.height = val
        })
        updateGroupMemberPosition(group);
        this.info = group;
        this.info.isFrozenMemberSty = false;  //重新计算成员样式
      },
      upGroupSize(type, val) {
        const group = _.cloneDeep(this.info);
        const one = group.member[0];
        if (type === "row") {
          group.height = (group.rows - 1) * val + group.rows * one.height;
          updateGroupMemberPosition(group)
        } else if (type === "col") {
          group.width = (group.cols - 1) * val + group.cols * one.width;
          updateGroupMemberPosition(group)
        }
        this.info = group;
        this.info.isFrozenMemberSty = false;  //重新计算成员样式
      },
      setNumber(evt, item) {
        let val = evt.target.value;
        let str = val.replace(/[^0-9]/ig, "");
        
        if (str && str.length>0){
          evt.target.value = parseInt(str)
          item.seatNum = parseInt(str)
        }
        else {
          evt.target.value = ""
          item.seatNum = ""
        }
      }
    }
  }
</script>