<template>
  <div class="input-box">
    <el-form :inline="true" :model="canvasSet" label-width="100px" class="demo-form-inline">
      <el-form-item label="画布宽">
        <el-input-number v-model="canvasSet.width" :controls="false" :precision="0" placeholder="自适应"></el-input-number>
      </el-form-item>
      
      <el-form-item label="画布高">
        <el-input-number v-model="canvasSet.height" :controls="false" :disabled="hasBg" :precision="0" placeholder="自适应"></el-input-number>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "WidthInput",
    props:{
      canvasSet: {
        type: Object,
        required: true
      },
      hasBg:{
        type: Boolean
      }
    },
    data() {
      return {

      }
    },
    watch: {
      canvasSet: {
        handler(val) {
          const self = this;
          const {width, height} = val;
          const lef = $(".seat-designer-page").offset().left;
          const top = $(".seat-designer-page").offset().top;
          const screenHit = $(window).height();
          const screenWid = $(window).width();
          if (width && (width + lef > screenWid)) {
            $(".seat-designer-center").css({"overflow-x": "scroll"});
          } else {
            $(".seat-designer-center").css({"overflow-x": "auto"});
          }
          if (height && (height + top > screenHit)) {
            $(".seat-designer-center").css("overflow-y", "scroll");
          } else {
            $(".seat-designer-center").css("overflow-y", "auto");
          }
          self.$store.commit("seatDesign/changeCompelPanelSize", val);
        },
        deep: true
      },
    },
  }
</script>