<template>
  <div class="ds-designer ds-seat-container ds-seat-design">
    <div class="designer-head">
      <top-tools v-bind.sync="postData" @save-layout="saveLayout"></top-tools>
    </div>
    <div class="designer-content">
      <div class="seat-designer-center">
        <left-tools></left-tools>
        <seat-canvas>
          <template v-slot:seat>
            <seat-item v-bind.sync="postData"></seat-item>
          </template>
          <template v-slot:seatPop>
            <seat-pop></seat-pop>
          </template>
        </seat-canvas>
        <right-tools></right-tools>
      </div>
    </div>
  </div>
</template>

<script>
  import topTools from "_platform/designer/seat/seatDesign/topTools.vue";
  import leftTools from "_platform/designer/seat/seatDesign/leftTools.vue";
  import rightTools from "_platform/designer/seat/seatDesign/rightTools.vue"
  import canvas from "_platform/designer/seat/seatDesign/canvas.vue";
  import seatItem from "_platform/designer/seat/seatDesign/seatItem";
  import seatPop from "_platform/designer/seat/seatDesign/seatPop"
  
  import {changePanelSizeForBgImage, sysBindShortKey} from "_platform/designer/seat/func";
  import eventBus from "_platform/designer/seat/util/eventBus.js";
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import ConfigBuilder from "_platform/designer/seat/util/configBuilder.js";


  export default {
    name: "DsfSeatDesign",
    components: {
      topTools,
      leftTools,
      rightTools,
      "SeatCanvas": canvas,
      seatItem,
      seatPop
    },
    data() {
      return {
        keyStack: [],
        
        //下面这些为提供给子组件的props
        postData: {
          _id: "",
          
          //用于top-tools组件的
          backgroundImage: {},
          canvasSet: {
            width: undefined,
            height: undefined
          },
          hasBg: false,
          
          //用于seat-item组件的
          items: [],
          groups: []
        }
      }
    },
    computed: {},
    watch:{
      "postData.backgroundImage"(){
        changePanelSizeForBgImage.call(this);
      }
    },
    
    created() {
      const layoutUrl = dsf.url.getWebPath("$/seat_config/seat/designSeatLayout.js");
      
      this.$http.importFiles(layoutUrl).then((res) => {
        this.layoutConfig = new ConfigBuilder(designSeatLayoutConfig, this.$route.query.opt);
        this.bindShortKey();
        this.getSeatInfo();
      });
    },
    beforeDestroy() {
      $(document).off("keydown");
      $(document).off("keyup");
    },
    
    methods: {
      saveLayout() {
        if(process.env.NODE_ENV === "development"){
          this.saveByDev();
        }
        else {
          this.saveByPub();
        }
      },
      bindShortKey() {
        const stopList = ["ctrl", "ctrl-d", "ctrl-s"];
        sysBindShortKey(this.keyStack, stopList);
        $("body").on("click", function () {
          eventBus.dispatch(eventBusEvent.BODY_EMPTY_CLICK);
        })
      },
      saveByDev(){
        const purePostData = getPurePostData.call(this);
        const id = this.$route.query.id || dsf.uuid();
        purePostData._id = id;
        
        localStorage.setItem("seat_"+id, JSON.stringify(purePostData));
        const originQuery = this.$route.query;
        const _query = Object.assign({}, originQuery, {id: id})
        this.$router.replace({path: this.$route.path, query: _query})
      },
      saveByPub(){
        const purePostData = getPurePostData.call(this);
        const data = {
          "pageName": "edit",
          "namespace": "dsfa.seat.designcontent",
          "data": JSON.stringify(purePostData)
        }
        const loadVm = dsf.layer.loading();
        this.$http.post(this.layoutConfig.saveLayoutUrl, data)
          .done(res => {
            if (!res.success) {
              dsf.layer.message("排座设计器保存失败", false);
              return
            }
            
            this.postData._id = res.data._id;
            this.$router.replace({path: this.$route.path, query: {id: res.data._id}})
            dsf.layer.message("保存成功");
          })
          .error(err => {
            dsf.layer.message("排座设计器保存失败", false);
          })
          .always(() => {
            dsf.layer.closeLoading(loadVm);
          })
      },
      
      getSeatInfo() {
        if (process.env.NODE_ENV === "development"){
          this.getInfoDev()
        }
        else {
          this.getInfoPub()
        }
      },
      getInfoDev(){
       const id = this.$route.query.id;
       if (!id) return;
       const data = JSON.parse(localStorage.getItem("seat_" + id));
       if (data){
         if (_.isEmpty(data.canvasSet)){
           data.canvasSet = {
             width: undefined,
             height: undefined
           }
         }
         this.postData = data;
       }
      },
      getInfoPub(){
        const id = this.$route.query.id;
      }
    },
  }
  
  function getPurePostData() {
    const purePostData = _.cloneDeep(this.postData);
    purePostData.items.forEach(el => {
      el.isFrozenSty = false
    })
    purePostData.groups.forEach(el => {
      el.isFrozenSty = false;
      el.isFrozenMemberSty = false;
    })
    return purePostData;
  }

</script>

<style type="text/scss" lang="scss">
  @import "~_platform/assets/styles/designer/seatDesign.scss";
</style>