import {eventBusEvent, uploadUrl} from './constant'
import eventBus from "_platform/designer/seat/util/eventBus";

/*seatDesign 里面使用的方法*/
export function uploadImg(formData, callback) {
  const self = this;
  const config = {
    headers: {'Content-Type': 'multipart/form-data'},
    transformRequest: [function (data, headers) {
      return data;
    }],
  };
  
  let loadingIndex = dsf.layer.loading();
  self.$http.post(uploadUrl, formData, config)
    .done(res => {
      if (!dsf.isDef(res.data) || !res.success) {
        dsf.layer.message(res.message, false);
        callback && callback("error")
        return false
      }
      
      callback && callback(res.data);
    })
    .error(err => {
      dsf.layer.message("上传图片失败", false);
      callback && callback("error");
    })
    .always(() => {
      dsf.layer.closeLoading(loadingIndex);
    })
}

export function updateGroupMemberPosition(group) {
  const member = group.member[0];
  let totalTop = group.height - member.height * group.rows;
  let totalLeft = group.width - member.width * group.cols;
  let eachTop = group.rows > 1 ? totalTop / (group.rows - 1) : 0;
  let eachLeft = group.cols > 1 ? totalLeft / (group.cols - 1) : 0;
  group.member.forEach(item => {
    if (item.rowIndex > 0) item.top = _.floor((item.height + eachTop) * item.rowIndex, 2);
    if (item.colIndex > 0) item.left = _.floor((item.width + eachLeft) * item.colIndex, 2);
  })
}

export function getMemberSpaceLeft(group) {
  const member = group.member[0];
  let totalLeft = group.width - member.width * group.cols;
  return group.cols === 1 ? 0 : _.floor(totalLeft / (group.cols - 1), 2)
}

export function getMemberSpaceTop(group) {
  const member = group.member[0];
  let totalTop = group.height - member.height * group.rows;
  return group.rows === 1 ? 0 : _.floor(totalTop / (group.rows - 1), 2)
}

//冻结其他对象(未被激活对象)的样式渲染
export function forzenOtherItems(isForzenMemberSty = true) {
  this.items.forEach(el => {
    el.isFrozenSty = true;
    this.selectedItems.forEach(select => {
      if (el.id === select.id) {
        el.isFrozenSty = false
      }
    })
  })
  if (this?.groups?.length > 0) {
    this.groups.forEach(el => {
      el.isFrozenSty = true;
      el.isFrozenMemberSty = true;
      this.selectedItems.forEach(select => {
        if (el.id === select.id) {
          el.isFrozenSty = false
          !isForzenMemberSty && (el.isFrozenMemberSty = false)
        }
      })
    })
  }
}

//解冻所有对象,重新渲染
export function unForzenAllItems() {
  this.items.forEach(el => {
    el.isFrozenSty = false
  })
  this.groups.forEach(el => {
    el.isFrozenSty = false;
    el.isFrozenMemberSty = false;
  })
}

export function createUploadFileInput(inputName = "dsf-seat-upload") {
  $(`input[name='${inputName}']`).remove();
  let upDOM = document.createElement("input");
  upDOM.type = "file";
  upDOM.accept = "image/*";
  upDOM.style.display = "none"
  upDOM.name = inputName;
  document.body.appendChild(upDOM);
  return upDOM
}

export function changePanelSizeForBgImage() {
  const self = this;
  if (_.isEmpty(self.postData.backgroundImage)) return; //如果没有背景则返回
  let bg_w = self.postData.backgroundImage.width;
  let bg_h = self.postData.backgroundImage.height;
  let box_w = self.postData.canvasSet.width || $(".seat-designer-page").width();   //若画布宽没有设置值
  let scale = box_w / bg_w;
  
  self.postData.canvasSet.width = box_w;
  self.postData.canvasSet.height = bg_h * scale;
  $("#panel_bg").css("background-image", "url(" + self.postData.backgroundImage.absolutePath + ")");
}

export function sysBindShortKey(keyStack, stopList) {
  let keyCode;
  const keyNum2Str = (keyCode) => {
    switch (keyCode) {
      case 17:
        keyCode = "ctrl";
        break;
      case 68:
        keyCode = "d";
        break;
      case 65:
        keyCode = "a";
        break;
      case 46:
        keyCode = "delete";
        break;
      case 16:
        keyCode = "shift";
        break;
      case 83:
        keyCode = "s";
        break;
      default:
        keyCode = null;
    }
    return keyCode
  }
  
  $("body").on("keydown", function (evt) {
    keyCode = keyNum2Str(evt.keyCode);
    if (!keyCode) return;
    let index = keyStack.indexOf(keyCode);
    if (index === -1) keyStack.push(keyCode);
    let keyStr = keyStack.join("-");
    if (stopList.includes(keyStr)) {
      evt.stopPropagation();
      evt.preventDefault();
    }
    
    if (keyStr === "ctrl-d") {
      eventBus.emit(eventBusEvent.KEY_CTRL_D);
    }
    if (keyStr === "delete") {
      eventBus.emit(eventBusEvent.KEY_DELETE)
    }
  });
  $("body").on("keyup", function (evt) {
    evt.stopPropagation();
    evt.preventDefault();
    keyCode = keyNum2Str(evt.keyCode);
    _.pull(keyStack, keyCode);   //删除对应的keyCode
  });
}


/*seatView 里面使用的方法*/
//创建一条新的人员入座记录
export function newRelative(seat, person) {
  return {
    id: seat.id,
    pid: seat.parentId,
    belong: seat.belong,
    currentState: seat.stateList[1],
    person: person,
    seatNum: seat.seatNum
  }
}