class EventBus {
  constructor() {
    this.events = {};
  }
  
  listen(event, callback){
    if (this.events[event] === undefined){
      this.events[event] = [callback];
    }
    else {
      const arr = this.events[event];
      if (!_.isArray(arr)){
        console.log(`请使用 on 来监听 ${event} 事件`)
        return
      }
      arr.push(callback)
    }
  }
  dispatch(event, params){
    const eventDeal = this.events[event];
    if (!eventDeal){
      console.log(event + "未使用listen 注册")
      return;
    }
    
    if (!_.isArray(eventDeal)){
      console.log(event + "被on注册, 应使用emit触发")
      return
    }
    eventDeal.forEach(a => {
      a(params)
    })
  }

  on(event, callback)  {
    if (this.events[event] === undefined){
      this.events[event] = callback
    }
    else if (_.isArray(this.events[event])){
      console.log(`请使用 listen 来监听 ${event} 事件`);
    }
    else {
      console.log(event + " 监听器重名");
    }
  }
  emit(event, params) {
    const eventDeal = this.events[event]
    if (!eventDeal){
      console.log(event + "未使用on 注册")
      return;
    }
    if (_.isArray(eventDeal)){
      console.log(event + "被listen注册, 应使用dispatch触发")
      return
    }
  
    eventDeal(params)
  }
  off(event) {
    this.events[event] = undefined;
  }
  clear(){
    this.events = {}
  }

  $getAllListener(){
    return Object.entries(this.events)
  }
}

const eventBus = new EventBus();
window.$simpleEventBus = eventBus;
export default eventBus